.wp-block-media-manager-media-player {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0.5em;
  border-radius: 50vh;
}
.wp-block-media-manager-media-player .wp-block-media-manager__item {
  margin: 0 5px 0 0;
  display: inline-block;
  align-self: center;
  max-width: var(--responsive--aligndefault-width);
  border-radius: 9999px;
  line-height: 0;
}
.wp-block-media-manager-media-player button {
  padding: 0;
  border: 0;
}
.wp-block-media-manager-media-player button:focus {
  outline: none;
}
.wp-block-media-manager-media-player button svg {
  fill: currentColor;
}
.wp-block-media-manager-media-player .is-small-size button {
  width: 24px;
  min-width: 24px;
  height: 24px;
}
.wp-block-media-manager-media-player .is-normal-size button {
  width: 36px;
  min-width: 36px;
  height: 36px;
}
.wp-block-media-manager-media-player .is-medium-size button {
  width: 48px;
  min-width: 48px;
  height: 48px;
}
.wp-block-media-manager-media-player .is-large-size button {
  width: 60px;
  min-width: 60px;
  height: 60px;
}